.h h2 {
    font-size: 0.9rem;
}

.h {
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 6rem;
    padding: 0.8rem;
    width: 85%;
    border-radius: 10px;
    background: rgba(0, 0, 0, 0.04);
}

.bar {
    position: absolute;
    width: 2px;
    background-color: black;
    height: 30px;
}

.profil {
    object-fit: cover;
    width: 60px;
    height: 60px;
    border-radius: 300px;
    margin-bottom: 1rem;
}

.actuel {
    padding: 0.7rem;
    gap: 1rem;
    width: 100%;
    display: flex;
    
}


@keyframes anim {
    from {
        opacity: 0;
    }

    to {
        opacity: 1;
    }
}


.name {
    font-size: 0.8rem;
    color: rgba(0, 0, 0, 0.56);
}

.actuelC {
    border-radius: 10px;
    background: rgba(179, 137, 99, 0.15);
    width: 85%;
    display: flex;
    flex-direction: column;

}

.actuelA {
    border-radius: 10px;
    background: rgba(18, 238, 159, 0.15);
    width: 85%;
    display: flex;
    flex-direction: column;

}

.ra {
    margin-left: 5rem;
    padding: 1rem;
    gap: 5rem;
    margin-top: -4rem;
    display: flex;
    justify-content: center;
    align-items: center;
}

.rA {
    padding: 1rem;
    margin-top: -4rem;
    display: flex;
    justify-content: center;
    align-items: center;
}


.x {
    width: 30px;
    height: 30px;
}

.c {
    margin-left: 1rem;
    height: 20px;
    display: flex;
    gap: 5rem;
}

.cA {
    display: flex;
    gap: 1rem;
}

.a {
    font-size: 0.8rem;
    color: #CF48FF;
}

.demande {
    text-decoration: underline;
}
.main {
    animation-name: anim;
    animation-duration: 2s;
    gap: 1.5rem;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
}