.head {
    display: flex;
    justify-content: center;
    padding: 1.2rem;
    align-items: center;
   
}


.profil {
    object-fit: cover;
    width: 200px;
    height: 200px;
    border-radius: 300px;
    margin-bottom: 1rem;
}

.h3 {
    font-size: 0.8rem;
    color: #D73939;
}

.h1 {

    font-size: 1rem;
    color:rgba(0, 0, 0, 0.69);
}

.btn {
    margin-bottom: 2rem;
    width: 60%;
    font-weight: 600;
    font-size: 13px;
    color: #FFFFFF;
    padding: 0.5rem;
    background: #6935D6;
    border-radius: 4px;
    border: none;
}

.logout {
    padding: 1rem;
    margin-left: 1rem;
    width: 70px;
    height: 70px;
}
.des {
    margin: 0.6rem;
    font-size: 0.8rem;
    color:rgba(0, 0, 0, 0.45);
}

.main {
    animation-name: anim;
    animation-duration: 2s;
    margin: 1rem;
    box-shadow: -5px -5px 9px #ffffff73, 5px 5px 7px #5e687949;
    margin-top: 1.5rem;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    gap: 0.5rem;
    
}

@keyframes anim {
    from {
        opacity: 0;
    }

    to {
        opacity: 1;
    }
}


.nxt {
    padding: 0.3rem;
    display: flex;
    justify-content: center;
    align-items: center;
}

.a {
    padding: 2rem;
    display: flex;
    justify-content: center;
    align-items: center;
    width: 20px;
    height: 20px;
    background-color: rgba(76, 23, 226, 0.17);
    border-radius: 200px;
}
.img {
    width: 100%;
    height: 100%;
}
