* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    font-family: 'Source Sans Pro', sans-serif;
    font-weight: bold;
    font-size: 19px;
  }

  .header {
    overflow: hidden;
    display: flex;
    justify-content: space-between;
    padding: 1.5rem;
  }
  
  .imgC img {
    width: 200px;
    height: 200px;
  }


  .imgC {
    overflow: hidden;
    display: flex;
    justify-content: center;
    align-items: center;
  }

  .card {
    padding-bottom: 2rem;
    margin: 1rem;
    width: 90%;
    box-shadow: rgba(99, 99, 99, 0.2) 0px 2px 8px 0px;
    margin-bottom: 4rem;
  }
  
  .h1 {
    padding: 2rem;
    margin-left: 0.8rem;
    font-size: 40px;
  }
  
  .small {
    font-size: 14px;
    display: flex;
    align-self: flex-start;
    color: red;
    margin-top: 0rem;
    margin-left: 2.3rem;
    font-weight: bold;
  }
  .form {
    overflow: hidden;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }


  .des {
    color: burlywood;
    text-decoration: underline;
    margin-top: 1rem;
    width: 75%;
  }
  .file {
    font-size: 0.9rem;
    width: 75%;
    margin-top: 1rem;
    margin-bottom: 1rem;
    color: rgba(0, 0, 0, 0.35);
  }
  .select {
    padding: 0.3rem;
    margin-top: 1rem;
    margin-bottom: 1rem;
    width: 75%;
    border: 2px solid rgba(0, 0, 0, 0.35);
    color: rgba(0, 0, 0, 0.35);
  }

  option {
    font-size: 10px;
  }
  
.Description {
  border: 1px solid black;
  height: 60px;
  margin-top: 1rem;
  font-size: 0.8rem;
  width: 75%;
}

  .input1,.input2 {
    color: rgba(0, 0, 0, 0.69);
    font-size: 1rem;
    outline: none;
    border: none;
    padding-bottom: 0.5rem;
    border-bottom: 2px solid rgba(0, 0, 0, 0.35);
    margin-bottom: 1rem;
    width: 75%;
  }
  
  .input1 {
    margin-top: 1rem;
  }
  
  .input2 {
    margin-top: 1rem;
  }
  
  input::placeholder {
    font-weight: bold;
    font-size: 16px;
    color: rgba(0, 0, 0, 0.35);
  }
  
  .input3 {
    font-size: 1rem;
    border-radius: 3px;
    color: white;
    background-color: #1566E0;
    margin-top: 1rem;
    border: none;
    padding: 0.7rem;
    width: 75%;
  }
  
  
  .p {
    color: red;
    width: 75%;
  }
  

  .a {
    font-size: 0.8rem;
    display: flex;
    margin-top: 1rem;
    margin-left: 11rem;
  }
  